import React, { useState, useEffect } from 'react';
import './../styles.css';
const AddPurchase = ({ updateBudget }) => {
    const [amount, setAmount] = useState('');
    const [category, setCategory] = useState('');
    const [note, setNote] = useState('');
    const [categories, setCategories] = useState([]); // Store full Category objects
    useEffect(() => {
        chrome.storage.local.get(['categories'], (result) => {
            if (result.categories) {
                setCategories(result.categories);
            }
            else {
                setCategories([]); // Fallback to an empty array
            }
        });
    }, []);
    const handleSave = () => {
        if (amount && category) {
            const purchase = {
                amount: parseFloat(amount),
                category,
                note,
                date: new Date().toLocaleDateString(),
            };
            // Save the purchase to local storage and update the budget
            chrome.storage.local.get(['purchases', 'budget'], (result) => {
                const updatedPurchases = result.purchases ? [...result.purchases, purchase] : [purchase];
                const updatedBudget = Object.assign({}, result.budget);
                // Find the selected category and update its spent amount
                const categoryToUpdate = categories.find((cat) => cat.name === category);
                if (categoryToUpdate) {
                    categoryToUpdate.spent += purchase.amount;
                    // Update the budget object
                    updatedBudget[category] = {
                        spent: categoryToUpdate.spent,
                        total: categoryToUpdate.total,
                    };
                }
                // Save back to local storage
                chrome.storage.local.set({ purchases: updatedPurchases, budget: updatedBudget }, () => {
                    updateBudget(); // Call the update function to refresh the View Budget page
                    setAmount(''); // Reset the form
                    setCategory('');
                    setNote('');
                });
            });
        }
    };
    // Calculate the budget preview
    const getBudgetPreview = () => {
        if (!amount || !category)
            return null;
        const selectedCategory = categories.find((cat) => cat.name === category);
        if (!selectedCategory)
            return null;
        const amountNum = parseFloat(amount);
        const newSpent = selectedCategory.spent + amountNum;
        const remainingBudget = selectedCategory.total - newSpent;
        return {
            newSpent,
            remainingBudget,
        };
    };
    const budgetPreview = getBudgetPreview();
    return (React.createElement("div", { style: { padding: '5px' } },
        React.createElement("h1", { className: 'header' }, "Add Expense"),
        React.createElement("div", { className: "amount-input-wrapper" },
            React.createElement("span", { className: "dollar-sign" }, "$"),
            React.createElement("input", { type: "number", value: amount, onChange: (e) => setAmount(e.target.value), placeholder: "0", className: "amount-input" })),
        React.createElement("div", { style: { marginBottom: '16px' } },
            React.createElement("label", null, "Category"),
            React.createElement("select", { value: category, onChange: (e) => setCategory(e.target.value), style: {
                    width: '100%',
                    padding: '8px',
                    marginTop: '8px',
                    borderRadius: '4px',
                    border: '1px solid #ccc',
                } },
                React.createElement("option", { value: "" }, "Select Category"),
                categories.map((cat) => (React.createElement("option", { key: cat.name, value: cat.name }, cat.name))))),
        React.createElement("div", { style: { marginBottom: '16px' } },
            React.createElement("label", null, "Note"),
            React.createElement("input", { type: "text", value: note, onChange: (e) => setNote(e.target.value), style: {
                    width: '100%',
                    padding: '8px',
                    marginTop: '8px',
                    borderRadius: '4px',
                    border: '1px solid #ccc',
                } })),
        budgetPreview && (React.createElement("div", { style: {
                marginBottom: '16px',
                padding: '10px',
                backgroundColor: '#f9f9f9',
                borderRadius: '4px',
                border: '1px solid #ccc',
            } },
            React.createElement("h3", null, "Budget Preview"),
            React.createElement("p", null,
                React.createElement("strong", null, category)),
            React.createElement("p", null,
                "New Total Spent: $",
                budgetPreview.newSpent.toFixed(2)),
            React.createElement("p", null,
                "Remaining Budget: $",
                budgetPreview.remainingBudget.toFixed(2)))),
        React.createElement("button", { className: 'add-button', onClick: handleSave }, "Save")));
};
export default AddPurchase;
