// Module
___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --dark-purple: #964FF7;
    --light-purple: #D1A7EE;
    --dark-green: #14705E;
    --medium-green: #39B985;
    --light-green: #C5FBC6;
}

.dark-purple {
    color: var(--dark-purple);
}

.light-purple {
    color: var(--light-purple);
}

.dark-green {
    color: var(--dark-green);
}

.medium-green {
    color: var(--medium-green);
}

.light-green {
    color: var(--light-green);
}

.header {
    font-family: 'Poppins', sans-serif !important;
    font-weight: 600 !important;
    font-size: 16px !important;
    text-align: center !important;
}

.category-name {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 13px;
}

.add-button {
    background-color: var(--dark-purple); 
    color: white; /* Set the text color to white */
    border: none; /* Remove any default borders */
    border-radius: 10px; /* Add a slight border radius for rounded corners */
    padding: 10px 15px; /* Add some padding for size */
    cursor: pointer; /* Change the cursor to a pointer on hover */
    font-family: 'Poppins', sans-serif; /* Ensure font matches the rest of the app */
    font-weight: 600; /* Optional: make button text bold */
    width: 100%;
    transition: transform 0.2s ease-in-out;
}

.add-button:hover {
    transform: scale(1.02); /* Grow on hover */
    background-color: var(--dark-purple) !important; /* Ensure the background stays purple */
}

/*nav bar styles */
.custom-navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: white;
    padding: 10px 0;
    border-radius: 24px 24px 30px 30px; /* Rounded top corners */
    box-shadow: 0px -2px 15px rgba(0, 0, 0, 0.1);
}

.nav-icon {
    flex: 1;
    text-align: center;
    cursor: pointer;
    transition: transform 0.2s ease-in-out;
    color: #cac9c9; /* Default color for icons */
}

.nav-icon:hover {
    transform: scale(1.2); /* Grow on hover */
}

.nav-icon.selected .icon {
    color: var(--dark-purple); /* Purple color for selected tab */
}

.icon {
    font-size: 32px !important;
    transition: color 0.2s ease-in-out; /* Smooth transition for color change */
}

/* Wrapper for the amount input and dollar sign */
.amount-input-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 160px; /* Adjust width to be less wide */
    padding: 10px 15px;
    background-color: #f9f9f9;
    border-radius: 30px; /* Creates the oval shape */
    border: 1px solid #ccc;
    margin: 20px auto; /* Centers the wrapper horizontally */
}

.dollar-sign {
    font-size: 24px; /* Slightly larger for better alignment */
    color: #888;
    text-align: center;
}

.amount-input {
    border: none;
    background-color: transparent;
    font-size: 24px; /* Larger font size for the placeholder '0' */
    color: var(--dark-purple); /* Use the dark purple from your color variables */
    text-align: center;
    width: 100%;
    outline: none;
    appearance: textfield; /* Removes the spinner in some browsers */
}

.amount-input::placeholder {
    color: var(--dark-purple); /* Purple color for the placeholder */
    opacity: 0.7;
}

/* Remove spinner buttons in Chrome, Safari, Edge, and Opera */
.amount-input::-webkit-outer-spin-button,
.amount-input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}




  

  
  
